*/

.profile-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profile-sec {
  height: 76vh;
  width: 80%;
  background-color: #ffffff;
  border: 1px solid lightgray;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  border-radius: 13px;
  margin-top: 60px;
}

.profile-img-sec {
  height: 80%;
}

.display-pic {
  width: 80px;
  height: 80px;
  border-radius: 40px;
  align-self: flex-start;
  margin-top: 20px;
}

.profile-input-el-sec {
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-sec {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

.label-el {
  margin-bottom: 10px;
}

.input-el {
  width: 260px;
  height: 32px;
  outline: none;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid lightgray;
}

@media screen and (max-width: 768px) {
  .profile-sec {
    flex-direction: column;
    align-items: center;
  }

  .profile-img-sec {
    align-self: center;
    margin-top: 20px;
  }

  .profile-input-el-sec {
    width: 100%;
    align-items: center;
  }

  .input-sec {
    width: 80%;
  }

  .input-el {
    width: 100%;
  }
}
